<template>
    <div class="playpage">
        <div class="nav" >
          <i class="el-icon-back" @click="$router.go(-1)"></i>
          <p>{{$route.query.name}}</p>
        </div>
        <div class="cd-wrapper">
            <div class="box">
                <div class="coverimg" ref="rotate" :style="{'background':'url('+imgurl+')','backgroundSize':'100px'}"></div>
            </div>
        </div>
        <div class="wrapper">
            <div class="lyric-box" >
                <div  ref="lyricUL" class="content">
                <p v-for="(item,index) in lyric" :key="index" :data-index='index'
            :class="{'active':currentline===index}" ref="lyric">{{item.text}}</p>
            </div>
        </div>
        </div>
        
         <div class="bottom">
             <ul>
                 <li>
                     <img src="../../assets/images/play-random.png" alt="">
                 </li>
                 <li>
                     <img src="../../assets/images/play-last.png" alt="">
                 </li>
                 <li @click="playSong">
                     <img src="../../assets/images/play-playing.png" alt="" v-if="!isplay">
                     <img src="../../assets/images/play-pause.png" alt="" v-if="isplay">
                 </li>
                 <li>
                     <img src="../../assets/images/play-next.png" alt="">
                 </li>
                 <li>
                     <img src="../../assets/images/play-list-more.png" alt="">
                 </li>
             </ul>
       
        
         <audio loop="loop"  id="myAudio" ref="myAudio"  @canplay="getDuration" @timeupdate="updateTime">
         
        </audio>
        
        </div>
       
      </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            id:'',
            name:'',
            obj:{
                text:'',
                time:''
            },
            lyric:[],//歌词
            imgurl:'',
            song:'',
            
            isplay:false,
            lyricCurrent:'',
            currentline:0,
            currentTime:0,

        }
    },
   async created(){
     this.id=this.$route.query.id
     this.name=this.$route.query.name
     this.imgurl=this.$route.query.img
     const {data:res}=await this.$http.get('/lyric',{params:{id:this.id}})
     console.log(res)
     this.song=`https://music.163.com/song/media/outer/url?id=${this.id}.mp3`
    //  this.lrc=res.lrc.lyric
     this.formatLyric(res.lrc.lyric)
     console.log(res.lrc.lyric)
    },
    
    methods:{
        getDuration(){
            this.$refs.myAudio.play()
             console.log (this.$refs.myAudio.duration)
        },
        updateTime(e){
            this.currentTime=e.target.currentTime
            // console.log(e)
            for (let i = 0; i < this.lyric.length; i++) {
                if (this.currentTime >= (this.lyric[i].time)&&this.currentTime< this.lyric[i+1].time) {
                        this.currentline = i
                        // console.log(this.$refs.lyricUL.style.)
                        this.$refs.lyricUL.style.transform = `translateY(${170 - (30 * (i + 1))}px)`
    //   }
    }
  }
        },
        // 播放
        playSong(){
            this.isplay=!this.isplay
             if(this.isplay){
                this.$refs.myAudio.src=this.song
            this.$refs.myAudio.play()
            // this.audio.playing = true;
		this.$refs.rotate.style.animationPlayState = 'running';
            }else{
                this.$refs.myAudio.pause()
                this.$refs.rotate.style.animationPlayState = 'paused';
		        // this.audio.playing = false;
            }
        },
  
        //格式化播放时长
        formatDt(time) {
            let dt = time / 1000;
            let m = parseInt(dt / 60);
            let s = parseInt(dt % 60); //这里用Math.ceil取整会更严谨些
            m >= 10 ? m : (m = "0" + m);
            s >= 10 ? s : (s = "0" + s);
            return m + ":" + s;
        },
        //传入初始歌词文本text
    formatLyric(text) {
        let arr = text.split("\n"); //原歌词文本已经换好行了直接通过换行符“\n”进行切割
         let row = arr.length; //获取歌词行数
         for (let i = 0; i < row; i++) {
         let temp_row = arr[i]; 
         let temp_arr = temp_row.split("]");//我们可以通过“]”对时间和文本进行分离
         let text = temp_arr.pop(); //获取歌词文本,temp_arr为时间了
         //再对剩下的歌词时间进行处理
         temp_arr.forEach(element => {
          let obj = {};
          let time_arr = element.substr(1, element.length - 1).split(":");//先把多余的“[”去掉，再分离出分、秒
          let s = parseInt(time_arr[0]) * 60 + Math.ceil(time_arr[1]); //把时间转换成与currentTime相同的类型，方便待会实现滚动效果
          obj.time = s;
          obj.text = text;
          this.lyric.push(obj); //每一行歌词对象存到组件的lyric歌词属性里
        });
      }
      this.lyric.sort(this.sortRule); //重新排列歌词
    
    },
    sortRule(a, b) { //设置一下排序规则
      return a.time - b.time;
    }
    }
}
</script>


<style lang='less' scoped>
.playpage{
    // position: relative;
    overflow: hidden;
    height: 100vh;
    background-color: #555;
}
 .nav {
   width: 100vw;
    height: 8vh;
    color: #fff;
    line-height: 50px;

    position: fixed;
    z-index: 99;
    
    text-align: center;
    .el-icon-back {
      display: inline-block;
      font-size: 20px;
      position: absolute;
      left:20px;
      top: 50%;
      transform: translateY(-50%);
    }
    p {
    
      font-size: 18px;
    }
  }
  .cd-wrapper{
      position: relative;
      top: 100px;
      left:50%;
  }
  .box{
      width: 150px;
      height: 150px;
      border-radius: 50%;
      background-color: black;
      opacity: 0.7;
      transform: translateX(-50%);
    .coverimg{
        position: absolute;
        background-color: #fff;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        top: 50%;
        left: 50%;
        transform-origin: center center;
        transform: translate(-50%,-50%) rotate(0deg);
        background-size: 100px;
	    background-repeat: no-repeat;
        background-position: center;
        animation: animations1 10s linear infinite forwards;
	    animation-play-state: paused;
    }
  }
//   旋转动画
  @keyframes animations1 {
	0% {
		transform: translate(-50%, -50%) rotate(0deg);
	}

	50% {
		transform: translate(-50%, -50%) rotate(180deg);
	}
	100% {
		transform: translate(-50%, -50%) rotate(360deg);
    }
  }
  .wrapper{
      position: relative;
       width: 100%;
      height: 80vh;
      .content{
          width: 100%;
         position: absolute;
          line-height: 30px;
            font-size: 14px;
            text-align: center;
            color: #ccc;
      }
  }
  .lyric-box{
      position: relative;
       top: 120px;
          width: 100%;
          height: 280px;
          overflow: hidden;
          
      .active{
          color: #fff;
        //   font-weight: bold;
          font-size: 16px;
      }
      
  }
  .bottom {
  width: 100%;
  height: 12vh;
  line-height:12vh;
  
  position: fixed;
  
  bottom: 0;
  left: 0;
  z-index: 9;
 ul{
     width: 80%;
     margin-left: 10%;
     display: flex;
     justify-content: space-between;
     overflow: hidden;
     img{
         width: 30px;
         height: 30px;
     }
     :nth-child(3){
         line-height: 80px;
         img{
             margin-top: 8px;
             width: 50px;
             height: 50px;
             
         }
     }
 }
 
}
</style>