<template>
    <div class="newsong">
        
        <transition name="fade">
            <Loading v-if="ishow"></Loading>
        </transition>
        <div class="topnav">
            <AppHeader>最新歌曲</AppHeader>
            <div class="navlist">
                <ul>
                    <li v-for="item in navlist" :key="item.type"
                     @click="ChangeType(item)" :class="{active:currentIndex===item.type}"> 
                        <p>{{item.name}}</p>
                    </li>
                </ul>
            </div>
        </div>
        
        <Scroll ref="scroll" :pullup='pullup' :top='120'  @pulldown="loadData">
   
        <div class="song">
            <ul class="songlist">
                <li v-for="(item ,index) in newsonglist" :key="index" class="listitem" @click="play(item)">
                    <div class="imgbox">
                        <img  v-lazy="item.album.picUrl" alt="">
                    </div>
                    <p>{{item.album.name}}</p>
                </li>
                <!-- 解决ul高度问题 -->
                <div style="clear:both;"></div>
            </ul>
        </div>
        
        </Scroll>

    </div>
</template>

<script>
import Scroll from '../common/Scroll'
import AppHeader from '../user/component/AppHeader'
import Loading from '../common/Loading'
export default {
    components:{
        AppHeader,
        Loading,
        Scroll
    },
    data(){
        return{
            navlist:[
                {
                    type:0,
                    name:'全部'
                },
                {
                    type:7,
                    name:'华语'
                },
                {
                    type:8,
                    name:'日本'
                },
                {
                    type:96,
                    name:'欧美'
                },
                {
                    type:16,
                    name:'韩国'
                }
            ],
            currentIndex:0,
            newsonglist:[],
            ishow:true,
            pullup:true
        }
    },
    methods:{
       async ChangeType(e){
            this.currentIndex=e.type
            this.ishow=true
            const{data:res}=await this.$http.get('/top/song',{params:{type:e.type}})
            //  console.log(res.data)
            this.newsonglist=res.data
            this.ishow=false
        },
        play(e){
            console.log(e)
            this.$router.push({path:'/play',query:{id:e.id,name:e.name,img:e.album.picUrl}})
        },
      loadData(){
          this.$nextTick(()=>{
              this.$refs.scroll.refresh()
          })
      }
        
    },
   async created(){
       const{data:res}=await this.$http.get('/top/song?type=0')
    //    console.log(res.data)
       this.newsonglist=res.data
       this.ishow=false
        this.loadData()
    },
    activated(){
   this.loadData()
 },
    mounted(){
        this.$nextTick(()=>{
            this.loadData()
        })
    }
}
</script>

<style lang='less' scoped>
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.newsong{
    overflow: hidden;
}
.topnav{
    width: 100%;
    position: fixed;
    z-index: 999;
 }
.navlist{
    width: 100%;
    height: 50px;
    background-color: #fff;
    box-sizing:content-box;
    ul{
        width:100%;
        display: flex;
        justify-content: space-around;
        border-bottom: 1px solid #eee;
        li {
            float: left;
            line-height: 50px;
            
        }
        .active{
            font-weight: bold;
            border-bottom: 2px solid #de464e;
        }
    }
}


.song{
        width: 100%;
       top: 120px;
        .songlist{
            width: 100%;
           
            .listitem{
                width: 100px;
                height: 170px;
                float: left;   
                box-sizing: border-box;  
                margin-left: 18px;           
                .imgbox{
                    width: 100%;
                    height: 100px;
                }
                img{
                   width: 100%;
                   height: 100%;
                }
                p{
                    width: 100%;
                    font-size: 12px;
                }
            }
          
    }
    }
</style>